header {
    padding: 30px 0;


    ul {
        margin: 0;
        padding: 0;
    }
    li {
        list-style: none;
    }

    .wrap {
        display: flex;
        flex-direction: row;
        align-items: center;
    }

    // Nav
    .nav {
        flex-grow: 1;
        display: flex;
        align-items: center;
        justify-content: end;
        
        ul {
            display: flex;
            gap: 30px;
            align-items: center;
        }

        a {
            display: inline-block;
            text-decoration: none;
            font-size: 1.1rem;
            font-family: "PorximaNova-Medium", sans-serif;
            display: inline-flex;
            align-items: center;
            gap: 10px;
        }

        .signin {
            padding: 10px 20px;
            background-color: $color-accent;
            border-radius: 5px;
        }

        .user { position: relative; }
        .user img {
            width: 40px;
            border-radius: 50%;
            cursor: pointer;
        }
    }

    .mobile-menu-icon {
        display: none;

        img {
            width: 20px;
        }
    }


    .user-menu {
        position: absolute;
        top: 100%;
        right: -20px;
        display: flex;
        flex-direction: column;
        background-color: #fff;
        z-index: 999;
        width: 200px;
        border: 1px solid #eee;
        box-shadow: 0 0 10px rgb(0 0 0 / 10%);
        padding: 10px 0;
        border-radius: 10px;
        display: none;

        hr {
            margin: 0;
            border: none;
            border-bottom: 1px solid #eee;
        }

        a, div {
            padding: 10px 20px;
            transition: all .2s;
            width: 100%;
        }

        .message {
            font-weight: 300;
        }

        a:hover {
            background-color: #eee;
        }

        .signout {
            color: #E04A3A;
        }
    }

    .user-menu.active {
        display: block;
    }

    .mobile-menu-icon {
        cursor: pointer;   
    }
}


@media (max-width: 800px) {
    header {
        .nav {
            ul {
                display: none;
            }
            .mobile-menu-icon {
                display: flex;
                align-items: center;
                gap: 10px;
                font-weight: bold;
                text-transform: uppercase;
            }
        }
    }
}
