html {
    font-size: 100%;
    box-sizing: border-box;
}

*, *::before, *::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: 'PorximaNova-Regular', sans-serif;
    line-height: 1.3;
    color: $color-text;
}


.wrap {
    width: 1000px;
    max-width: 90%;
    margin: 0 auto;
}

.highlight {
    position: relative;
    width: auto;
    display: inline-block;
}

.highlight .marker {
    content: "";
    display: block;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background-color: $color-accent;
    z-index: 5;
}

.highlight .marker-50 {
    height: 50%;
}

.highlight .text {
    z-index: 10;
    position: relative;
}

.logo {
    height: 30px;
    position: relative;
    display: flex;
    align-items: center;
    text-transform: uppercase;

    span {
        position: relative;
        top: 2px;
        padding-left: 10px;
        font-weight: bold;
        font-size: 1.3rem;
        line-height: .9;
    }

    .sub {
        font-weight: 300;
        font-size: 1rem;
        padding: 0;
    }
}


img {
    height: 100%;
}

a {
    text-decoration: none;
    color: $color-text;
    transition: all .2s;
    position: relative;
    display: inline-block;
}

a:not(.ignore, .read-post a) {
    &::after {
        content: "";
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $color-accent;
        z-index: 5;
        transform: scaleX(0);
        transform-origin: left;
        transition: all .2s;
    }

    &:hover {
        color: $color-accent;
        
        &::after {
            transform: scaleX(1);
        }
    }
}


.section {
    margin: 80px 0 0 0;
    padding: 40px 0;
}

.section-both {
    margin: 80px 0;
    padding: 40px 0;
}

.overlay-trans {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0);
    z-index: 99;
}


.flash {
    padding: 20px;
    border: 2px solid $color-text;
    background-color: rgba($color: $color-text, $alpha: 0.2);
    border-radius: 10px;
    font-size: 1.3rem;

    a {
        color: $color-text;
        text-decoration: underline;
    }
}

.flash.success {
    background-color: rgba($color: #00ff00, $alpha: 0.2);
    border-color: #00ff00;
}

.flash.error {
    background-color: rgba($color: #FF0000, $alpha: 0.2);
    border-color: #FF0000;
}
