footer {
    border-top: 1px solid #eee;
    
    .wrap {
        display: flex;
        // align-items: center;
        gap: 20px;
        flex-wrap: wrap;
        justify-content: space-between;
    }

    .col {
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: flex-start;
    }

    .logo {
        height: 25px;

        span {
            font-size: 1rem;
        }
    }

    h3, .logo {
        margin-bottom: 10px;
        margin-top: 0;
    }

    .row {
        display: flex;
        flex-direction: row;
        gap: 10px;
        flex-wrap: wrap;
    }

    .social a {
        width: 40px;
        height: 40px;
        background-color: $color-text;
        padding: 10px;
        border-radius: 4px;
    }


    .copyright {
        margin: 40px 0;
        padding: 20px 0;
        border-top: 1px solid #eee;
        border-bottom: 1px solid #eee;
        width: 100%;

        .location {
            font-weight: 500;
            font-size: 1rem;
            width: 600px;
            max-width: 100%;
        }

        .rights {
            font-size: 0.9rem;
            margin-bottom: 0;
        }

        .aff {
            margin: 0;
            font-size: 0.9rem;
            opacity: 0.8;
        }
    }
}