.pagination {
    width: 100%;
    margin-top: 80px;
    font-size: 1.3rem;
    display: flex;
    gap: 80px;
    justify-content: center;

    a {
        padding: 10px 20px;
        border-radius: 5px;
        transition: all .2s;
    }

    a:hover {
        background-color: #eee;
    }

    .disabled {
        pointer-events: none;
        opacity: .5;
    }
}