.read-post-top-details {
    img {
        width: 100%;
    }

    .details {
        padding: 20px 0;
        width: 800px;
        margin: auto;
    }

    .meta {
        display: flex;
        align-items: center;
        gap: 10px;

        .info {
            display: flex;
            flex-direction: column;
            flex: 1;
            line-height: 1;
        }

        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }

    .date {
        font-size: .9rem;
    }

    .author {
        font-size: 1.1rem;
    }

    h1 {
        margin: 0;
        white-space: word-wrap;
    }
}

.read-post {
    width: 800px;
    margin: auto;
    max-width: 100%;
    line-height: 1.4;
    
    p {
        font-size: 1.3rem;
    }

    h1, h2, h3, h4, h5, h6 {
        margin: 40px 0 20px 0;
    }

    a {
        text-decoration: underline;

        &:hover {
            color: $color-accent;
        }
    }

    ul, ol {
        font-size: 1.3rem;
        display: flex;
        flex-direction: column;
        gap: 10px;
    }

    code:not(pre code) {
        background-color: #F5F2F0;
        padding: 3px 5px;
        border-radius: 5px;
        font-size: 1.1rem;
    }

    pre {
        font-size: 1.1rem;

        code {
            border-radius: 5px;
        }
    }

    .hljs {
        background: #FAF8F7;
    }

    table {
        overflow-x: auto;
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        table-layout: fixed;

        th, td {
            padding: 15px;
            border: 1px solid #ccc;
        }

        th {
            background-color: #f7f7f7;
        }
    }

    blockquote {
        border-left: 5px solid #ccc;
        padding: 0 15px;
        margin: 0 0 20px 0;
        color: rgba($color: $color-text, $alpha: 0.7)
    }

    img {
        width: 1000px;
        position: relative;
        left: -100px;
    }
}


@media (max-width: 1000px) {
    .read-post {
        img {
            max-width: 100%;
            left: 0;
        }
    }
}