.post-list {
    display: flex;
    flex-wrap: wrap;
    gap: 40px;

    .cat-title {
        margin: 0;
        font-weight: 400;
        border-bottom: 1px solid #eee;
        width: 100%;
        padding: 10px;
    }

    .search-form {
        width: 100%;
    }

    .search-box {
        padding: 20px;
        border: none;
        background-color: #eee;
        font-size: 1.3rem;
        width: 100%;
        border-radius: 5px;
        font-weight: 400;
    }

    .post {
        display: inline-flex;
        flex-direction: column;
        width: 47%;
    }

    .title a:hover {
        text-decoration: underline;
    }

    .image {
        height: 200px;
    }

    .title {
        margin: 20px 0 0 0;
        font-size: 1.7rem;
    }

    .meta {
        display: flex;
        align-items: center;
        gap: 10px;
        margin-top: 20px;

        .info {
            display: flex;
            flex-direction: column;
            flex: 1;
            line-height: 1;
        }

        img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
        }
    }

    .date {
        font-size: .9rem;
    }

    .author {
        font-size: 1.1rem;
    }

    .description {
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 4; /* number of lines to show */
                line-clamp: 4; 
        -webkit-box-orient: vertical;
        margin: 10px 0 0 0;
        font-size: 1.2rem;
        color: rgba($color: $color-text, $alpha: 0.7);
    }


    .post.first {
        min-width: 100%;
        flex-direction: row;
        flex-wrap: wrap;

        .image {
            width: 580px;
            height: 300px;
            max-width: 100%;
        }

        .details {
            flex: 1;
            padding: 20px;
        }

        .title {
            margin: 0;
        }
    }
}


@media (max-width: 850px) {

    .post-list {

        .post {
            width: 100%;
            .image {
                height: 300px;
            }
        }


        .post.first {
            flex-direction: column;

            .details { padding-left: 0; }
            .image { width: 100%; }
        }

    }

}