#mobile-nav {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: -100%;
    background-color: #fff;
    z-index: 99;
    box-shadow: 0px 0px 10px rgba($color: #000000, $alpha: 0.5);
    transition: right 0.3s ease-in-out;
    height: 100%;
    width: 300px;
    max-width: 100%;

    .list {
        display: flex;
        flex-direction: column;
        overflow-y: auto;
    }

    hr {
        width: 100%;
        border: 0;
        height: 1px;
        background-color: #ccc;
        margin: 0;
    }

    .user {
        display: flex;
        align-items: center;
        padding: 20px;
        gap: 10px;

        p {
            margin: 0;
        }
        p:first-child {
            font-size: 1.3rem;
        }

        p:last-child {
            font-size: 1rem;
            opacity: 0.8;
        }

        img {
            width: 80px;
            border-radius: 50%;
        }
    }

    a {
        display: block;
        padding: 10px 20px;
        font-size: 1.3rem;
        transition: all .2s;
        display: flex;
        align-items: center;
        gap: 10px;
    }

    a:hover {
        background-color: #eee;
    }

    .signout {
        color: #E04A3A;
    }

    .spacer {
        flex: 1;
    }

    #close {
        text-align: center;
        padding: 20px;
        background-color: #eee;
    }

    .signin {
        padding: 10px 20px;
        background-color: $color-accent;
        border-radius: 5px;
        text-align: center;
        margin: 20px;
    }
}

#mobile-nav-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba($color: #000000, $alpha: 0.5);
    z-index: 98;
    display: none;
}

